import 'Verselist.css'
import verseData from "verseData"
import VerseListItem from 'VerseListItem'
import MainVisual from 'MainVisual'
import Arrow from 'Arrow'
import { isMobile } from 'globals'



export default class VerseList extends React.Component {
	constructor(p){
		super(p)
		let {listBg, verseList, verse, dragBtn} = this
		this.state = {
			Listhidden: false
		}
	}
	setBackground(e){
		let { listBg, verseList } = this
		let header = document.getElementsByTagName('header')
		let id = e.currentTarget.getAttribute('data-id')

		if(!isMobile){
			listBg.style.backgroundImage = `url(${process.env.PUBLIC_PATH}asset/${id}.jpg)`
			listBg.style.transition = '.5s'
			listBg.style.opacity = '.5'
			header[0].style.backgroundColor = 'transparent'
		}
	}
	noBackground = () =>{
		let { listBg, verseList } = this
		let header = document.getElementsByTagName('header')

		listBg.style.transition = '.5s'
		listBg.style.opacity= '0'
		header[0].style.backgroundColor = '#EBEBEC'
	}
	componentDidMount(){
		let { dragBtn } = this
		let clientX

		dragBtn.addEventListener('touchstart', (e) => {
			clientX = e.touches[0].clientX
		}, false)
		dragBtn.addEventListener('touchmove', (e) => {
			let deltaX
			deltaX = e.changedTouches[0].clientX - clientX
		
			if(deltaX >= 200){
				this.setState({ ...this.state, Listhidden: !this.state.Listhidden })
			}else{
				dragBtn.style.transform = `translateX(${deltaX}px)`
			}
		})
		dragBtn.addEventListener('touchend', (e) => {
			let deltaX
			deltaX = e.changedTouches[0].clientX - clientX
			if(deltaX >= 200){
				this.setState({ ...this.state, Listhidden: !this.state.Listhidden })
			}else{
				dragBtn.style.transform = `translateX(0px)`
			}

			console.log(deltaX)
		})
	}
    render() {
    	let { Listhidden } = this.state
        return  <section className="verselist page" ref={ el => this.verseList = el }>
        		<div className={`interaction ${ Listhidden && 'hidden' }`} style={{ backgroundImage: `url(${process.env.PUBLIC_PATH}asset/bg.jpg)` }}>
        			<div className="container">
        				<div className="main-visual">
        					<MainVisual></MainVisual>
        				</div>
        				<div className="interaction-nav">
        					<div className="drag-square" ref={ el => this.dragBtn = el }>
        						<div className="arrow">
        							<Arrow></Arrow>
        						</div>
        					</div>
        					<div className="connecting-line">
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        						<span></span>
        					</div>
        					<div className="target-square"></div>
        				</div>
        				<div className="illustration">
        					<span>點擊並拖曳</span>
        				</div>
        			</div>
        		</div>
                <div className={`listBg ${ Listhidden && 'visible' }`} 
                	 ref={ el => this.listBg = el }>
                </div>
                <div className={`verselist ${ Listhidden && 'visible' }`}>
                	{verseData == null ? null : 
                	    verseData.map((e, id) => <VerseListItem key={id} item={e} 
                	    										onMouseOver={(e) => this.setBackground(e)} 
                	    										onMouseLeave={this.noBackground}
                	    										/>)}
                </div>
                

        </section>
    }
}


